import { Modal, Table, Button } from "antd";
import moment from "moment";
import React, { Fragment, useState, useEffect } from "react";
import "./BookingHistory.css";

export default function BookingHistory(props) {
  let data = props.thongTinDatVe;
  data.map((item, index) => {
    data[index] = { ...data[index], stt: index + 1 };
  });
  // console.log(data);

  const [isModalVisible, setIsModalVisible] = useState(false);
  // const [ticketCode, setTicketCode] = useState(0);

  const showModal = (maVe) => {
    setIsModalVisible(true);
    // setTicketCode(maVe);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };
  const handleOk = () => {
    setIsModalVisible(false);
  };

  // const soGhe = [
  //         {
  //           title: "H thng rp",
  //           dataIndex: "tenHeThongRap",
  //           //   width: 100,
  //           align: "center",
  //         },
  //         {
  //           title: "S rp",
  //           dataIndex: "tenCumRap",
  //           align: "center",
  //         },
  //         {
  //           title: "S gh",
  //           dataIndex: "tenGhe",
  //           align: "center",
  //         },
  //       ];

  const columns = [
    {
      title: "STT",
      dataIndex: "stt",
      //   width: 100,
      align: "center",
    },
    {
      title: "Tn phim",
      dataIndex: "tenPhim",
      align: "center",
    },
    {
      title: "Thi lng",
      dataIndex: "thoiLuongPhim",
      render: (thoiLuongPhim, dataCurrent) => {
        return thoiLuongPhim + " pht";
      },
      align: "center",
    },
    {
      title: "Ngy gi t",
      dataIndex: "ngayDat",
      render: (ngayDat, dataCurrent) => {
        return moment(ngayDat).format("DD/MM/YYYY - hh:mm:ss");
      },
      align: "center",
    },
    {
      title: "Gi v",
      dataIndex: "giaVe",
      render: (giaVe, dataCurrent) => {
        return giaVe + " VN";
      },
      align: "center",
    },
    {
      title: "S gh",
      dataIndex: "danhSachGhe",

      render: (danhSachGhe, dataCurrent) => {
        const soGhe = [
          {
            title: "H thng rp",
            dataIndex: "tenHeThongRap",
            //   width: 100,
            align: "center",
          },
          {
            title: "S rp",
            dataIndex: "tenCumRap",
            align: "center",
          },
          {
            title: "S gh",
            dataIndex: "tenGhe",
            align: "center",
          },
        ];
        return (
          <Fragment>
            <button className="p-1 bg-pink-600 rounded" onClick={showModal}>
              Chi tit
            </button>

            <Modal
              title="Thng tin gh  t"
              visible={isModalVisible}
              onOk={handleOk}
              onCancel={handleCancel}
              key={dataCurrent.maVe}
            >
              <Table
                columns={soGhe}
                dataSource={danhSachGhe}
                rowKey={(ghe) => ghe.maGhe}
                onChange={onChange}
                pagination={{ pageSize: 3 }}
              />
            </Modal>
          </Fragment>
        );
      },
    },
    {
      dataIndex: "tenHeThongRap, tenRap, tenGhe",
      align: "center",
    },
  ];

  function onChange(pagination, filters, sorter, extra) {
    // console.log("params", pagination, filters, sorter, extra);
  }

  return (
    <div className="booking-history bg-gray-blue-color w-full rounded-lg shadow-xl">
      <div className="p-4 border-b">
        <h2 className="text-2xl text-white">Lch s t v</h2>
        <p className="text-sm text-gray-300 ">
          Bm vo chi tit  xem s gh!
        </p>
      </div>
      <Table
        columns={columns}
        dataSource={data}
        rowKey={(ve) => ve.maVe}
        onChange={onChange}
        // scroll={{ y: "50vh" }}
        pagination={{ pageSize: 3 }}
      />
      {/* <Modal
              title="Thng tin gh  t"
              visible={isModalVisible}
              onOk={handleOk}
              onCancel={handleCancel}
              key={dataCurrent.maVe}
            >
              <Table
                columns={soGhe}
                dataSource={danhSachGhe}
                rowKey={(ghe) => ghe.maGhe}
                onChange={onChange}
                pagination={{ pageSize: 3 }}
              />
            </Modal> */}
    </div>
  );
}
